const globalState = {};

const subscribe = (key, observer) => {
  globalState[key]._observer = observers.add(observer);
};

const unsubscribe = (key, observer) => {
  globalState[key]._observer = observers.delete(observer);
};

const _notify = (key) =>
  globalState[key]._observers.forEach((observer) => observer());

const initState = ({ key, defaultValue }) => {
  if (key in globalState) throw new Error("the key is already existing");
  globalState[key] = {
    _state: defaultValue,
    _observer: new Set(),
  };
  return key;
};

const getState = (key) => {
  if (!(key in globalState)) throw new Error("the key is already");
  return globalState[key]._state;
};

const setState = (key) => (newState) => {
  if (!(key in globalState)) throw new Error("the key is already");

  if (typeof newState === "function") {
    const state = getState(key);
    globalState[key]._state = newState(state);
  } else {
    globalState[key]._state = newState;
  }
  _notify(key);
};

export { subscribe, unsubscribe, initState, getState, setState };
