import { subscribe, unsubscribe } from "../lib/Observer";
import dom from "../utils/dom";

export default class Component {
  constructor(type = "div", props) {
    this.$target = dom.createElement({ tagName: type });
    this.props = props;
    this.state = this.initState();

    this.keys = [];
    this.components = {};

    this.init();
    this.reRender = this.render.bind(this);
  }

  init() {
    this.render();
    this.addEvent();
  }

  initState() {
    return;
  }

  addEvent() {
    return;
  }

  render() {
    this.components = this.setComponents();
    this.$target.innerHTML = this.setTemplate();
    this.setLayout();
  }

  //innerHTML
  setTemplate() {
    throw new Error("í…œí”Œë¦¿ì„ ìƒì„±í•´ì£¼ì„¸ìš”.");
  }

  //ì»´í¬ë„ŒíŠ¸ë¥¼ ê° ìœ„ì¹˜ì— ë§ê²Œ replace
  setLayout() {
    for (const [key, Comp] of Object.entries(this.components)) {
      const replacedElem = this.$target.querySelector(`#${key}`);
      console.log(Comp);
      replacedElem?.replaceWith(Comp.$target);
    }
  }

  setComponents() {
    return {};
  }

  //í´ë˜ìŠ¤ ì¶”ê°€ ë©”ì†Œë“œ
  addClass(...args) {
    this.$target.className = args.join(" ");
  }

  //êµ¬ë…í•œ ìƒíƒœ ë³€ê²½ì‹œ ë Œë”ë§ ë˜ëŠ” í•¨ìˆ˜
  subscribedRender() {
    this.unsubscribe(); //í•˜ìœ„ ì»´í¬ë„ŒíŠ¸ êµ¬ë… í•´ì œ
    this.render(); //í•˜ìœ„ ì»´í¬ë„ŒíŠ¸ ì¬ìƒì„±
  }

  //keyì— ì†í•œ ê²ƒë“¤ render
  subscribe() {
    this.keys.forEach((key) => subscribe(key, this.reRender));
  }

  unsubscribe(isCurrentComp = true) {
    if (!isCurrentComp && this.keys.length) {
      this.keys.forEach((key) => unsubscribe(key, this.reRender));
    }

    //í•˜ìœ„ ì»´í¬ë„ŒíŠ¸ë“¤ë„ ë¦¬ë Œë”ë§ë˜ê¸° ë•Œë¬¸ì— unsubscribe
    const components = Object.values(this.components);
    components.forEach((component) => {
      component.unsubscribe(false);
    });
  }

  //TODO: í•˜ìœ„ ì „ë¶€ë‹¤ ë Œë”ë§ ë˜ëŠ” ê²ƒ í•´ê²°
  //TODO: throw Errorë¥¼ ì—ëŸ¬ì²˜ë¦¬ë¥¼ ë”°ë¡œ í•´ë†“ì§€ ì•ŠëŠ”ë‹¤ë©´ ë°°í¬ ë‹¹ì‹œì— ì‚­ì œí•´ì•¼ë˜ë‚˜??
  setState(newState) {
    if (!this.state) throw Error("ë³€ê²½í•  ìƒíƒœê°€ ì—†ìŠµë‹ˆë‹¤!");

    if (typeof newState === "function") {
      this.state = { ...this.state, ...newState(this.state) };
    } else {
      this.state = { ...this.state, ...newState };
    }
    this.reRender();
  }
}
